<template>
  <v-card>
    <v-card-title>
      <v-text-field
        v-model="search"
        append-icon="mdi-magnify"
        label="Search"
        single-line
        hide-details
      ></v-text-field>
    </v-card-title>
    <v-data-table
      :headers="headers"
      :items="data"
      :search="search"
      :loading="!data.length"
      loading-text="En cours de chargement..."
      group-by="date_visite"
      class="elevation-1"
      show-group-by
    >
      <template v-slot:top>
      <v-toolbar
        flat
      >
        <v-toolbar-title>Visites</v-toolbar-title>
        <v-divider
          class="mx-4"
          inset
          vertical
        ></v-divider>
         </v-toolbar>
      </template>
        <v-spacer></v-spacer>
    <template v-slot:no-data>
      <v-btn
        color="primary"
        @click="initialize"
      >
        Reset
      </v-btn>
    </template>
    </v-data-table>
  </v-card>
</template>
<script>
import axios from 'axios'
  export default {
    name: 'Visite',
    props:['clientId'],
    data () {
      return {
        search: '',
        headers: [
          { text: 'ID', value: 'id', align: 'right',groupable: false},
          { text: 'Date visite', value: 'date_visite', align: 'right'},
          { text: 'Patient', value: 'client', align: 'right' ,groupable: false},
          { text: 'Motif', value: 'motif', align: 'right',groupable: false },
          { text: 'Utilisateur', value: 'utilisateur', align: 'right'},
        ],
        data:[],
      }  
    },
    methods:{
      
      initialize(){
        let id = this.clientId
        const options = {
          headers:{
            'x-access-token': 'Bearer ' +localStorage.getItem("token")
          }
        }
        fetch(`/visite/${id}`, options).then(resp => {
            if(resp.status == 401){
              this.$router.push('/login')
            }else
              return resp.json()
          }).then(data =>{
            console.log(data)
            this.data = data
          }).catch(err => {
            console.log(err)
            this.$router.push('/login')
        })
      },
    },
    computed: {
     
    },
    watch: {
    
    },
    created:function(){
      this.initialize()
    }
  }
</script>